@import "vars";
@import "mixins";

.bgimg-1 {
    background-image: url('../images/contact/contact.jpg');
    min-height: 500px;
}

#socials {
    height: 10px;
    width: 100%;
    justify-content: space-around;
    margin-bottom: 200px;
}

.social {
    margin: 40px 20px;
    width: 275px;
    background: #979292;
    height: 60px;
    border-radius: 5px;
    overflow: hidden;
    @include justify-content(left);

}

.socialColor {
    width: 5px;
    height: 100%;
}

#twitter>.socialColor {
    background: #1da1f2;
}

#discord>.socialColor {
    background: #7289da;
}

#github>.socialColor {
    background: #171516;
}

.socialImg {
    width: 60px;
    height: 60px;
    border-right: 1px dotted black;
    img {
        width: 50px;
        height: 50px;
        padding: 5px;
    }
}

.tag a {
    display: block;
    margin: 17px 3px;
    text-decoration: none;
    font-size: 25px;
}

.twitterTag a {
    color: #1da1f2;
}

.discordTag a {
    color: #7289da;
}

.githubTag a {
    color: #171516;
}